@charset "UTF-8";
.body,
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-family: "Poppins";
}

ul {
  list-style: none;
}

h1,
h2,
thead {
  font-family: "Poppins", Verdana, Geneva, sans-serif;
  font-weight: 400;
  font-size: 1.3rem;
  text-decoration: overline;
  padding: 35px;
}
@media (min-width: 37.5em) {
  h1,
h2,
thead {
    font-size: 2rem;
  }
}
@media (min-width: 50em) {
  h1,
h2,
thead {
    font-size: 2.3rem;
  }
}

.services-calc-form,
.select-calc,
.find-us,
.footer-main,
.about-us-text {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  text-align-last: center;
}

.flex-contact-info p,
.flex-contact-info a {
  font-size: 0.9rem;
  line-height: 1;
  text-align: center;
}
@media (min-width: 37.5em) {
  .flex-contact-info p,
.flex-contact-info a {
    font-size: 1.5rem;
  }
}

@media (min-width: 37.5em) {
  .item {
    font-size: 1.5rem;
  }
}

@media (min-width: 50em) {
  .toggle i {
    font-size: 2rem;
  }
}

.services-calc {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  text-align-last: center;
}
.services-calc p {
  font-size: 1.2rem;
  font-weight: 900;
}
@media (min-width: 50em) {
  .services-calc p {
    font-size: 1.4rem;
  }
}

.services-table {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
}
@media (min-width: 37.5em) {
  .services-table {
    font-size: 1.5rem;
  }
}
@media (min-width: 50em) {
  .services-table {
    font-size: 2rem;
  }
}

.services-calc-form .select-calc,
.services-calc-form .select-calc:invalid {
  font-size: 0.9rem;
}
@media (min-width: 37.5em) {
  .services-calc-form .select-calc,
.services-calc-form .select-calc:invalid {
    font-size: 1rem;
  }
}
@media (min-width: 50em) {
  .services-calc-form .select-calc,
.services-calc-form .select-calc:invalid {
    font-size: 1.4rem;
    font-weight: 400;
  }
}
.services-calc-form .select-calc option,
.services-calc-form .select-calc:invalid option {
  font-size: 0.9rem;
}
.services-calc-form label {
  line-height: 0;
  font-size: 1.4rem;
}
@media (min-width: 37.5em) {
  .services-calc-form label {
    font-size: 1.3rem;
    font-weight: 400;
  }
}
@media (min-width: 50em) {
  .services-calc-form label {
    font-size: 1.6rem;
  }
}
.services-calc-form input {
  font-size: 1.2rem;
}

.carousel-text {
  font-family: "Poppins";
  font-weight: bolder;
  color: #ffffff;
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 50em) {
  .carousel-text {
    font-size: 2.5rem;
  }
}

.about-us {
  text-align: center;
}
.about-us-text {
  font-size: 0.9rem;
  line-height: 1.4;
}
@media (min-width: 37.5em) {
  .about-us-text {
    font-size: 1.3rem;
  }
}
@media (min-width: 50em) {
  .about-us-text {
    font-size: 1.6rem;
  }
}

.review-content {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  font-size: 0.9rem;
}
@media (min-width: 37.5em) {
  .review-content {
    font-size: 1.2rem;
    font-weight: 400;
  }
}
@media (min-width: 50em) {
  .review-content {
    font-size: 1.6rem;
  }
}

.btn-primary {
  font-family: "Poppins";
  color: #333333;
}
@media (min-width: 37.5em) {
  .btn-primary {
    font-size: 1.1rem;
  }
}
@media (min-width: 50em) {
  .btn-primary {
    font-size: 1.5rem;
  }
}

@media (min-width: 37.5em) {
  .footer-main {
    font-size: 1.1rem;
  }
}
@media (min-width: 37.5em) {
  .footer-main {
    font-size: 1.4rem;
  }
}

.btn-primary {
  padding: 10px 15px;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid #919191;
  border-radius: 8px;
  background-color: #ffffff;
}
.btn-primary#calculate {
  margin-top: 20px;
}
.btn-primary#see-more {
  margin-top: 10px;
}
@media (min-width: 37.5em) {
  .btn-primary#see-more {
    margin: 25px;
  }
}
.btn-primary:hover {
  transition: 0.5s cubic-bezier(0.47, 0, 0.75, 0.72);
  background: #4f95da;
  color: #ffffff;
}

@media (min-width: 37.5em) {
  .btn-primary {
    padding: 15px 20px;
  }
}
.carousel {
  box-sizing: content-box;
  position: relative;
  height: 252px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  margin-bottom: 30px;
  overflow: hidden;
}
@media (min-width: 50em) {
  .carousel {
    height: 450px;
  }
}
.carousel .carousel-slide img {
  height: 250px;
}
@media (min-width: 37.5em) {
  .carousel .carousel-slide img {
    width: 100%;
    object-fit: cover;
  }
}
@media (min-width: 50em) {
  .carousel .carousel-slide img {
    height: 450px;
  }
}

.carousel > div {
  position: absolute;
}

.carousel-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.fade-in {
  opacity: 0;
  transition: opacity 250ms ease-in;
}

.fade-in.appear {
  opacity: 1;
}

.from-left {
  grid-column: 2/3;
  transform: translateX(-50%);
}

.from-right {
  grid-column: 3/4;
  transform: translateX(50%);
}

.from-left,
.from-right {
  transition: opacity 250ms ease-in, transform 600ms ease-in;
  opacity: 0;
}

.from-left.appear,
.from-right.appear {
  transform: translateX(0);
  opacity: 1;
}

.flex-header {
  background-color: #cddeef;
  column-gap: 70px;
  padding: 10px 0;
}
.flex-header .header-image {
  width: 13vw;
}
@media (min-width: 37.5em) {
  .flex-header {
    font-size: 1.3rem;
  }
  .flex-header .header-image {
    width: 15vw;
  }
}

.flex-contact-info {
  margin: auto;
}
.flex-contact-info p,
.flex-contact-info a {
  font-family: "Open Sans", Impact, Haettenschweiler, sans-serif;
  font-weight: 200;
  color: #333333;
  padding: 1px;
  overflow: hidden;
}

a:hover {
  text-decoration: underline;
}

nav {
  background: #222;
  padding: 0 15px;
}

.menu {
  padding: 13px 5px;
}
.menu .item {
  padding: 10px;
  order: 3;
  width: 100%;
  text-align: center;
  display: none;
}
.menu .item a {
  display: block;
  padding: 15px 5px;
  color: #ffffff;
}
.menu .item a:hover::after {
  color: #ccc;
}
.menu .toggle {
  order: 1;
  font-size: 1.38rem;
  margin-left: auto;
}
.menu .toggle i {
  color: #ffffff;
}
@media (min-width: 37.5em) {
  .menu body {
    background-color: #c79393;
  }
  .menu .menu {
    justify-content: center;
  }
  .menu .toggle {
    text-align: right;
    order: 2;
  }
}
@media (min-width: 70em) {
  .menu .menu {
    background: none;
  }
  .menu .item {
    order: 1;
    position: relative;
    display: block;
    width: auto;
  }
  .menu .toggle {
    display: none;
  }
  .menu .submenu-active {
    border-radius: 0;
  }
  .menu .submenu-active .submenu {
    border-left: inset;
    background: #111;
    display: block;
    left: 0;
    position: absolute;
    top: 74px;
    z-index: 1;
    width: 294px;
    /* text-align: left; */
    text-align-last: left;
  }
  .menu .submenu-active {
    border-radius: 0;
  }
  .menu .toggle {
    display: none;
  }
}

.active .item {
  display: block;
  cursor: pointer;
}

.menu li.subitem a {
  padding: 10px;
  text-align: center;
}

.submenu {
  display: none;
}

.submenu-active {
  background-color: #111;
  border-radius: 3px;
}

.submenu-active .submenu {
  display: block;
}

.has-submenu i {
  font-size: 0.63rem;
}

.has-submenu > a::after {
  font-family: "Font Awesome 5 Free";
  font-size: 0.63rem;
  line-height: 16px;
  font-weight: 900;
  content: "";
  color: #ffffff;
  padding-left: 5px;
}

.subitem a {
  padding: 10px 15px;
}

.services-container {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}
.services-container .services-calc {
  width: 320px;
  margin-top: 27px;
  padding-top: 40px;
  align-items: center;
  background-color: #fdfdfd;
  border: 1px solid #333333;
}
@media (min-width: 37.5em) {
  .services-container .services-calc {
    height: 580px;
    width: 500px;
    justify-content: space-evenly;
    padding: 5px;
  }
  .services-container .services-calc h2 {
    padding: 35px;
    margin-top: 15px;
  }
}
@media (min-width: 50em) {
  .services-container .services-calc {
    width: 700px;
    height: 800px;
  }
}
.services-container .services-calc p {
  padding-top: 20px;
  padding-bottom: 10px;
}
.services-container .services-calc .result-span {
  color: #f30a0a;
  margin-bottom: 10px;
}

.services-calc-form {
  height: 250px;
  justify-content: center;
  align-items: center;
  padding: 6px 20px;
  margin-top: 10px;
  background-color: #cddeef;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
}
@media (min-width: 37.5em) {
  .services-calc-form {
    height: 500px;
  }
}
@media (min-width: 50em) {
  .services-calc-form {
    width: 450px;
    padding: 30px;
  }
}
.services-calc-form .select-calc {
  cursor: pointer;
  padding: 5px 10px;
  margin-bottom: 20px;
}
.services-calc-form .select-calc:invalid {
  color: #5e5e5e;
}
@media (min-width: 37.5em) {
  .services-calc-form .select-calc {
    padding: 10px;
    margin-top: 20px;
  }
}
.services-calc-form .select-calc option {
  color: #333333;
}
.services-calc-form .calc-form-input {
  padding: 5px 10px;
  width: 70%;
  border-radius: 6px;
}
@media (min-width: 50em) {
  .services-calc-form .calc-form-input {
    padding: 10px;
    margin-top: 20px;
    width: 150px;
  }
}

.services-table {
  border-collapse: collapse;
  color: #333333;
  align-items: center;
  width: 100%;
  margin-bottom: 20px;
}
.services-table thead {
  padding: 10px;
  margin-top: 25px;
}
.services-table tr {
  border-bottom: 1px dotted black;
}
.services-table td {
  padding: 6px;
}

.services-container,
.services-table,
.services-calc,
.services-calc-form,
.review {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

@media (min-width: 37.5em) {
  .services-calc-form {
    justify-content: space-evenly;
  }
}

.flex-header,
.menu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
@media (min-width: 37.5em) {
  .flex-header .flex-header,
.menu .flex-header {
    padding: 10px 30px;
  }
}

.menu {
  flex-wrap: wrap;
  align-items: center;
}
@media (min-width: 37.5em) {
  .menu .menu {
